.tab-list {  border-bottom: 1px solid rgb(7, 231, 7);
    padding-left: 0;
    background-color: rgb(104, 172, 107);
  }
  
  .tab-list-item {
    display: inline-block;
    list-style: none;
    margin-bottom: -1px;
    padding: 0.5rem 0.75rem;
  }
  
  .tab-list-active {
    background-color: rgb(15, 132, 168);
    border: solid rgb(87, 88, 71);
    border-width: 1px 1px 0 1px;
  }